import React from 'react';
import cloudly from './wi-cloud.svg';
let class_item ={
'Item':'item_Item__B-O8p',
'Sub':'item_Sub__10sLJ'
}
const Item = () => {
	return (
	<div className={class_item.Item}> 
		<span>Сегодня</span>
		<br></br>
		<h3>2 июня</h3>
		<br></br>
		<img src ={cloudly}/>
		<br></br>
		<h4> днем +26<sup>o</sup></h4>
		<h5> ночью +19<sup>o</sup></h5>
		
		<div className={class_item.Sub}>
			<center><h6>облачно,</h6></center>
			<center><h6>без осадков</h6></center>
		</div>
	</div>	
	);
}
export default Item;